.mobile__button {
	padding: 10px 0;
	float: right;
}

.mobile__button span {
	position: relative;
	display: block;
	width: 27px;
	height: 3px;
	background: #000000;
	transition: all 0.3s ease-out;
	backface-visibility: hidden;
}

.mobile__button span:before,
.mobile__button span:after {
	position: absolute;
	content: '';
	display: block;
	width: 27px;
	height: 3px;
	background-color: #000000;
	backface-visibility: hidden;
	transition: all 0.3s ease-out;
}

.mobile__button span:before {
	top: -8px;
}

.mobile__button span:after {
	bottom: -8px;
}

.mobile__button.open span {
	background: transparent;
}

.mobile__button.open span:before {
	background: #000000;
	top: 0px;
	left: 0px;
	transform: rotate(45deg);
}

.mobile__button.open span:after {
	background: #000000;
	bottom: 0px;
	left: 0px;
	transform: rotate(-45deg);
}

@media (min-width: 640px) {
	.mobile__button {
		display: none;
	}
}
